import {ref, firebaseAuth} from '../../data/config'

const saveUser = (user) => {
    ref
        .child(`users/${user.uid}/info`)
        .set({
            email: user.email,
            uid: user.uid
        })
        .then(() => user)
}

const auth = (email, pass) => (firebaseAuth().createUserWithEmailAndPassword(email, pass).then(saveUser))

const login = (email, pass) => firebaseAuth().signInWithEmailAndPassword(email, pass)

const logout = () => firebaseAuth().signOut()

const resetPassword = email => firebaseAuth().sendPasswordResetEmail(email)

export {saveUser, auth, login, logout, resetPassword}