import React from 'react'
import uid from 'uid'

import Calendar from './Calendar'
import MultiSelect from './MultiSelect'
import {categories, teachers} from '../../data'

import 'pure-css/lib/forms.css'
import 'pure-css/lib/buttons.css'
import './course-add-form.css'

const optionsTeachers = teachers.map(teacher => Object.assign({}, {
    label: teacher,
    value: teacher
}))

const optionsCategories = categories.map(cat => Object.assign({}, {
    label: cat,
    value: cat
}))

const CourseAdd = props => (
    <form onSubmit={props.onAddCourse} className="pure-form AddForm">
        <input type="text" placeholder="Nombre del curso" name="name"/>
        <input type="url" name="poster" id="poster" placeholder="Poster"/>
        <input type="url" name="url" id="url" placeholder="Url"/>
        <input type="number" name="amount" id="amount" placeholder="Costo"/>
        <MultiSelect
            name="teacher"
            placeholder="Elige el profesor (es) del curso"
            options={optionsTeachers}/>
        <MultiSelect
            name="categories"
            placeholder="Elige la categoria (as) del curso"
            options={optionsCategories}/>
        <Calendar name="date"/>
        <input type="hidden" name="id" value={uid(10)}/>
        <input type="submit" value="Guardar" className="pure-button pure-button-primary"/>
    </form>
)

export default CourseAdd